.clearfix:after, .clearfix:before { content: " "; display: table; clear: both; }

a { text-decoration: none; }

a:hover { text-decoration: none; }

a:visited { text-decoration: none; }

a:active { text-decoration: none; }

a:focus { text-decoration: none; }

body { height: 100%; font: 16px/24px 'Open Sans', sans-serif; background-color: #fff; }

.wrapper { width: 990px; padding: 0 15px; margin: 0 auto; }

.header { background-color: #cc3739; padding: 20px 30px; margin-bottom: 20px; display: flex; flex-direction: row; justify-content: space-between; }

.header__head { color: #ebd5d6; }

.header__gh { width: 25px; }

.posts.no-author .posts__name { display: none; }

.posts__head { display: table-cell; color: #cc3739; font-size: 20px; cursor: pointer; }

.posts__head:hover { text-decoration: underline; }

.posts__head:first-letter { text-transform: uppercase; }

.posts__body { margin-bottom: 30px; margin-top: 5px; }

.posts__body:first-letter { text-transform: uppercase; }

.posts__name { color: #cc3739; cursor: pointer; }

.posts__name:hover { text-decoration: underline; }

.posts__name:before { content: 'Author: '; font-weight: bold; color: #000; text-decoration: none; display: inline-block; margin-right: 5px; }

.user { flex-direction: column; align-items: flex-start; margin-bottom: 15px; display: none; }

.user:before { content: 'User info:'; font-weight: bold; }

.user:after { content: 'User posts:'; font-weight: bold; margin-top: 5px; }

.user__name:before { content: 'Name: '; }

.user__username:before { content: 'Username: '; }

.comments { position: relative; display: none; }

.comments:before { content: 'Comments:'; font-size: 18px; font-weight: bold; margin-bottom: 15px; display: block; }

.comments__item { margin-bottom: 20px; }

.comments__item:before { content: 'Author: '; font-weight: bold; }

.comments__body { margin: 0; }
